<template>
  <div class="hello">
    <div>{{count}}</div>
    <button v-on:click="countup">増やす</button>
    <button v-on:click="countdown">減らす</button>
  </div>
</template>

<script>
import { getDatabase, ref, onValue, set} from "firebase/database";

export default {
  name: 'Counter',
  data() {
    return {
      count : 0
    }
  },
  methods:{
    countup: function ()
    {
      const db = getDatabase();
      const countRef = ref(db, 'counter');
      set(countRef, this.count + 1);
    },
    countdown: function ()
    {
      const db = getDatabase();
      const countRef = ref(db, 'counter');
      set(countRef, this.count - 1);
    }
  },
  mounted: function () {
    const db = getDatabase();
    const countRef = ref(db, 'counter');
    onValue(countRef, (snapshot) => {
      const data = snapshot.val();
      this.count = data;
    });
  }
}
</script>

